import Layout from "../components/MyLayout";
import Head from "next/head";
import fetch from "isomorphic-unfetch";
import { Component } from "react";

class Post extends Component {
  constructor(props) {
    super(props);
    this.state = {
      article: props.article
    };
  }
  render() {
    console.log(this.state.article.images[0].mainUrl);
    return (
      <React.Fragment>
        <Head>
          <meta charset="utf-8" />
          <link rel="stylesheet" href="/static/test.css" />
        </Head>
        <Layout>
          <div className="container">
            <h1>{this.state.article.title}</h1>
            <div className="wrapper">
              <section className="main">
                <img src={`http://${this.state.article.images[0].mainUrl}`} />
              </section>
              <header>
                <img
                  src="https://pbs.twimg.com/profile_images/378800000147359764/54dc9a5c34e912f34db8662d53d16a39_400x400.png"
                  alt="Quincy Larson's profile picture"
                  className="profile-thumbnail"
                />
                <div class="profile-name">
                  <h3>Quincy Larson</h3>
                  <h4>@ossia</h4>
                </div>
                <div class="follow-btn">
                  <button>Follow</button>
                </div>
              </header>
            </div>
          </div>

          {/* <p>{props.show.summary.replace(/<[/]?p>/g, "")}</p> */}
          {/* <img src={`http://${this.state.article.images[0].mainUrl}`} /> */}
        </Layout>
      </React.Fragment>
    );
  }
}

Post.getInitialProps = async function(context) {
  const { id } = context.query;
  const res = await fetch(
    `https://interview-project-17987.herokuapp.com/api/article/article-${id}`
  );
  const article = await res.json();

  console.log(`Fetched show: ${article.url}`);

  return { article };
};

export default Post;
